import React from 'react'
import Button from "../ButtonElem.js"
import { InfoWrapper,InfoRow,Column1,Column2,TextWrapper,ImgWrap,InfoContainer,TopLine,Heading,Subtitle,BtnWrap,Img } from './InfoSection'
import img1 from "../../images/1.svg"
const InfoSection = ({
    lightBg,
    id,
    imgStart,
    topLine,
    headline,
    description,
    darkText,
    buttonlabel,
    lightText,
    alt,primary,dark,dark2}) => {
    return (
        <>
           <InfoContainer lightBg={lightBg} id={id}>
              <InfoWrapper>
                 <InfoRow imgStart={imgStart}>
                     <Column1>
                         <TextWrapper>
                             <TopLine>{topLine}</TopLine>
                             <Heading lightText={lightText}>{headline}</Heading>
                             <Subtitle darkText={darkText}>{description}</Subtitle>
                             <BtnWrap> 
                             <Button to='home'
                             smooth={true} duration={500} spy={true} exact="true" offset={-80}
                             primary={primary ?1:0} dark={dark ? 1 : 0} dark2={dark2 ? 1 : 0}
                             >{buttonlabel}</Button>
                             </BtnWrap>
                         </TextWrapper>
                     </Column1>
                     <Column2>
                         <ImgWrap>
                             <Img src={img1} alt={alt}/>
                         </ImgWrap>
                     </Column2>
                 </InfoRow>
              </InfoWrapper>
           </InfoContainer> 
        </>
    )
}

export default InfoSection
