import React from 'react'
import { FaBars} from 'react-icons/fa'
import {MobileIcon, Nav, NavbarContainer, NavItem, NavLinks, Navlogo, NavMenu,NavBtn,NavBtnLink} from "./Navelem"
 const Navbar = ({toggle}) => {
    return (
        <>
        <Nav>
            <NavbarContainer>
                <Navlogo to="/">Career-Track</Navlogo>
                <MobileIcon onClick={toggle}>
                    <FaBars />
                </MobileIcon>
                <NavMenu>
                    <NavItem>
                        <NavLinks to='about'>About</NavLinks>
                    </NavItem>
                    <NavItem>
                        <NavLinks to='service'>Service</NavLinks>
                    </NavItem>
                    <NavItem>
                        <NavLinks to='content'>Content</NavLinks>
                    </NavItem>
                    <NavItem>
                        <NavLinks to='/signup'>Sign up</NavLinks>
                    </NavItem>
                </NavMenu>
                <NavBtn>
                    <NavBtnLink to="/signin">Sign In</NavBtnLink>
                </NavBtn>
            </NavbarContainer>
        </Nav>
        </>
    )
}
export default Navbar